<template>
	<view>
		<view class="top">
			<image src="../../static/image/back.png" mode="widthFix" class="back-img" @click="goback"></image>
			<image src="../../static/image/close.png" mode="widthFix" class="close-img"></image>
		</view>
		<image src="../../static/image/logo.png" mode="widthFix" class="logo-img"></image>
		<view class="title">注册</view>

		<u-form :model="regForm" ref="regFormRef">
			<u-form-item prop="name" class="form-item"><u-input v-model="regForm.name" placeholder="请取个名字" /></u-form-item>
			<u-form-item prop="email" class="form-item"><u-input v-model="regForm.email" placeholder="请输入邮箱"  /></u-form-item>
			<u-form-item prop="password" class="form-item"><u-input v-model="regForm.password" placeholder="请输入密码" type="password" :clearable="false" /></u-form-item>
		</u-form>
		<button type="default" @click="register" class="register-button">注册</button>
	</view>
</template>

<script>
export default {
	data() {
		return {
			regForm: {
				name: '',
				email: '',
				password: ''
			},
			rules: {
				name: [
					{ 
						required: true, 
						message: '请输入姓名', 
						// 可以单个或者同时写两个触发验证方式 
						trigger: 'blur'
					}
				]
			}
		};
	},
	// 必须要在onReady生命周期，因为onLoad生命周期组件可能尚未创建完毕
	onReady() {
		this.$refs.regFormRef.setRules(this.rules);
	},
	methods: {
		register() {
			uni.navigateTo({
				url: '../login/login'
			});
		},
		goback() {
			uni.navigateBack();
		}
	}
};
</script>

<style lang="scss" scoped>
.top {
	display: flex;
	justify-content: space-between;
	align-items: center;
	padding: 50rpx;
	.back-img {
		width: 42rpx;
	}
	.close-img {
		width: 34rpx;
	}
}
.logo-img {
	display: block;
	width: 170rpx;
	margin: 50rpx auto;
}
.title {
	font-size: 22px;
	font-weight: bold;
	margin-left: 50rpx;
	margin-bottom: 30rpx;
}
/deep/ .u-form-item{
	margin: 0 50rpx;
	height: 120rpx;
}
/deep/ .u-form-item__message{
	padding-left: 0 !important;
}
/deep/ .u-input{
	border-bottom: 1px solid #ddd;
}
.register-button {
	width: 500rpx;
	height: 90rpx;
	line-height: 90rpx;
	border-radius: 50px;
	margin: 100rpx auto;
	background-color: rgb(133, 203, 248);
	color: #fff;
	font-weight: bold;
	&::after {
		border: none;
	}
}
</style>
